body {
    background: #11998e;  /* fallback for old browsers */
background: -webkit-linear-gradient(to bottom, #38ef7d, #11998e);  /* Chrome 10-25, Safari 5.1-6 */
background: linear-gradient(to bottom, #38ef7d, #11998e); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
min-width: 100%;
min-height: 100%;
width:100%;
height: 100vh;
overflow: hidden;
}
.section {
    position: relative;
	width: 100%;
	display: block;
}
.full-height {
    height: 100vh;
}
.over-hide {
    overflow: hidden;
}
.absolute-center {
	position: absolute;
	top: 50%;
	left: 0;
	width: 100%;
  margin-top: 40px;
	transform: translateY(-50%);
	z-index: 2;
}


.border-ahora {
    border-color: #11998e;
}
.input-group-text{
    background: #11998e;
    color:white;
    border-color: #11998e;
    width:100%;
}

@media (max-width:500px) {
    .ahoracambio {
        display: block;
    }
}

@media (max-width:400px) {
    .ahoracambio {
        display: none;
    }
}